.container {
  margin: 80px auto 0;
  max-width: 450px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content {
  width: 100%;
  background: #FFF;
  margin-top: 30px;
  border-radius: 10px;
  padding: 30px;
}

.content>p {
  font-size: 24px;
  font-weight: 600;
  line-height: 30px;
  margin-bottom: 20px;
  color: #484848;
}

.alert {
  font-size: 14px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 20px;
  padding: 10px;
  color: #fff;
  border-radius: 5px;
}

.alert-error {
  background-color: #f1328f;
}

.alert-dark {
  background-color: #000;
}

.alert-info {
  background-color: #03a7d3;
}

.alert-oops {
  text-align: center;
  color: #777777;
  font-size: 14px;
  font-weight: 600;
}

.alert-oops img {
  width: 300px;
}

.content form {
  display: flex;
  flex-direction: column;
}

.content form label {
  font-size: 14px;
  color: #444;
  font-weight: bold;
  margin-bottom: 8px;
}

.content form label span {
  font-weight: normal;
  color: #999;
  font-size: 12px;
}

.content form input, .content form select {
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 2px;
  height: 45px;
  padding: 0 15px;
  font-size: 16px;
}

option { 
  background-color: white; 
  color:black;
}

.content button.btn {
  border: 0;
  border-radius: 5px;
  /* width: 100%; */
  height: 42px;
  padding: 0 20px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
}

.btn-round {
  padding: 0px !important;
  width: 42px;
  border-radius: 100% !important;
}

.btn-success {
  background: #4ab955;
}

.btn-success:hover {
  background: #3fb64b;
}

.content button.btn-primary {
  background: #ff3d9a;
}

.content button.btn-primary:hover {
  background: #f1328f;
}

.content button.btn-dark {
  background: #000;
}

.content button.btn-dark:hover {
  background: #0e1013;
}

.btn-full {
  width: 100%;
}

.btn-logout {
  margin-top: 10px;
}

.ml-5 {
  margin-left: 5px;
}

.Toastify__toast--error {
  background: #ff3d9a !important;
}

.Toastify__toast--success {
  background: #4ab955 !important;
}

.Toastify__toast--warning {
  background: #f4f060;
  color: #000;
}

small.link {
  margin-top: 20px;
}

small.link a {
  color: #ff3d9a !important;
  font-weight: bold;
}

.react-confirm-alert-overlay {
  background: rgba(0, 0, 0, 0.5) !important;
}

.react-confirm-alert-button-group :first-child {
  background: #ff3d9a !important;;
  color: #fff !important;
  font-weight: bold;
}


.react-confirm-alert-body > h1 {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 22px;
}