label#thumbnail {
  /* margin-bottom: 20px; */
  border: 1px dashed #ddd;
  background-size: cover;
  cursor: pointer;
  height: 160px;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

label#thumbnail input {
  display: none;
}

label#thumbnail.has-thumbnail {
  border: 0;
}

label#thumbnail.has-thumbnail img {
  display: none;
}

.btn-success {
  background: #4ab955;
}

.btn-success:hover {
  background: #3fb64b;
}

#equipoForm input, #equipoForm select {
  margin-bottom: 5px;
}

#equipoForm label {
  margin-top: 10px;
}

#equipoForm button {
  margin-top: 10px;
}

#equipoForm .alert-form {
  color: #f44336;
  font-weight: 600;
}

#equipoForm .alert {
  margin-top: 10px;
  margin-bottom: 5px;
}
