.container {
  margin: 80px auto 0;
  max-width: 450px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.content {
  width: 100%;
  background: #FFF;
  margin-top: 30px;
  border-radius: 10px;
  padding: 30px;
}

.content>p {
  font-size: 24px;
  font-weight: 600;
  line-height: 30px;
  margin-bottom: 20px;
  color: #484848;
}

.alert {
  font-size: 14px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 20px;
  padding: 10px;
  color: #fff;
  border-radius: 5px;
}

.alert-error {
  background-color: #f1328f;
}

.alert-dark {
  background-color: #000;
}

.alert-info {
  background-color: #03a7d3;
}

.alert-oops {
  text-align: center;
  color: #777777;
  font-size: 14px;
  font-weight: 600;
}

.alert-oops img {
  width: 300px;
}

.content form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.content form label {
  font-size: 14px;
  color: #444;
  font-weight: bold;
  margin-bottom: 8px;
}

.content form label span {
  font-weight: normal;
  color: #999;
  font-size: 12px;
}

.content form input, .content form select {
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 2px;
  height: 45px;
  padding: 0 15px;
  font-size: 16px;
}

option { 
  background-color: white; 
  color:black;
}

.content button.btn {
  border: 0;
  border-radius: 5px;
  /* width: 100%; */
  height: 42px;
  padding: 0 20px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
}

.btn-round {
  padding: 0px !important;
  width: 42px;
  border-radius: 100% !important;
}

.btn-success {
  background: #4ab955;
}

.btn-success:hover {
  background: #3fb64b;
}

.content button.btn-primary {
  background: #ff3d9a;
}

.content button.btn-primary:hover {
  background: #f1328f;
}

.content button.btn-dark {
  background: #000;
}

.content button.btn-dark:hover {
  background: #0e1013;
}

.btn-full {
  width: 100%;
}

.btn-logout {
  margin-top: 10px;
}

.ml-5 {
  margin-left: 5px;
}

.Toastify__toast--error {
  background: #ff3d9a !important;
}

.Toastify__toast--success {
  background: #4ab955 !important;
}

.Toastify__toast--warning {
  background: #f4f060;
  color: #000;
}

small.link {
  margin-top: 20px;
}

small.link a {
  color: #ff3d9a !important;
  font-weight: bold;
}

.react-confirm-alert-overlay {
  background: rgba(0, 0, 0, 0.5) !important;
}

.react-confirm-alert-button-group :first-child {
  background: #ff3d9a !important;;
  color: #fff !important;
  font-weight: bold;
}


.react-confirm-alert-body > h1 {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 22px;
}
.survey-radio input[type=radio], .survey-check input[type=checkbox]{
    display:none;
}
.survey-radio input[type=radio] + label, .survey-check input[type=checkbox] + label{
    display:inline-block;
    height:20px;
    padding:0 0 0 25px;
    margin:0 10px 0 0;
    background-image:url(/static/media/ico-master.f6ac8fdf.webp);
    background-repeat:no-repeat;
    background-position:0 0;
}
.survey-check input[type=checkbox] + label{
    background-position:0 -60px;
    margin-bottom: 5px;
}
.survey-radio input[type=radio]:checked + label{
    background-position:0 -30px;
}
.survey-check input[type=checkbox]:checked + label {
    background-position:0 -90px;
}

.survey-check {
    margin-top: 20px;
    margin-bottom: 20px;
}

.subtitle {
    margin-bottom: 10px;
}

ul.equipo-list {
  width: 100%;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  margin-bottom: 30px;
}

ul.equipo-list li {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s; /* Animation */
  padding: 10px;
}

ul.equipo-list li:hover {
  /* -webkit-box-shadow: -1px 0px 32px -1px rgba(168,168,168,1);
  -moz-box-shadow: -1px 0px 32px -1px rgba(168,168,168,1);
  box-shadow: -1px 0px 32px -1px rgba(168,168,168,1); */
  /* border-radius: 5px; */
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

ul.equipo-list li header {
  width: 100%;
  height: 120px;
  background-size: contain;
  border-radius: 5px;
  background-repeat: no-repeat;
}

ul.equipo-list li strong {
  margin-top: 10px;
  font-size: 16px;
  color: #444;
}

ul.equipo-list li span {
  font-size: 15px;
  color: #999;
}

ul.notifications {
  list-style: none;
  margin-bottom: 15px;
}

ul.notifications li {
  font-size: 16px;
  line-height: 24px;
}

ul.notifications li button {
  margin-right: 10px;
  border: 0;
  font-weight: bold;
  margin-top: 10px;
  cursor: pointer;
}

ul.notifications li button.accept {
  color: #84C870;
}

ul.notifications li button.reject {
  color: #E55E5E;
}

.btn-loc {
  margin-top: 10px;
}

#search .btn-search {
  border-radius: 0px 5px 5px 0px !important;
}

#search button {
  height: 45px !important;
}

#search input {
  border-radius: 5px 0px 0px 5px !important;
  height: 45px !important;
}

nav {
  grid-template-columns: 4fr 1fr;
  display: grid;
  margin-bottom: 30px;
}

nav .welcome {
  text-align: left;
}

nav .actions {
  text-align: right;
}

