.equipo-table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 390px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.equipo-table thead tr {
  background-color: #ff3d9a;
  color: #ffffff;
  text-align: left;
}

.equipo-table th,
.equipo-table td {
    padding: 12px 15px;
}

.equipo-table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.equipo-table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.equipo-table tbody tr:last-of-type {
  border-bottom: 2px solid #ff3d9a;
}

.equipo-table tbody tr.active-row {
  font-weight: bold;
  color: #ff3d9a;
}

.delete-icon {
  color: #ff3d9a;
  text-align: right;
