}

.thumbnail {
  border-radius: 100px;
}

.search {
  width: 100%;
  position: relative;
  display: -webkit-flex;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 1px solid #ff3d9a;
  border-right: none;
  padding: 5px;
  height: 46px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9DBFAF;
}


.searchTerm:focus{
  color: #000;
  border: 1px solid #ff3d9a;
}

.searchButton {
  width: 50px;
  height: 46px;
  border: 1px solid #ff3d9a;
  background: #ff3d9a;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.searchButton svg {
  margin-top: 5px;
}

.search input {
  margin-bottom: 0px !important;
}
